body {
  margin: 0;
}

.product-card {
  box-sizing: border-box;
  color: #060b35;
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.product-card__image {
  box-sizing: border-box;
  background-size: cover;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__name {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__code:before {
  content: "Product code: ";
}

.product-card__score {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 23px;
  display: flex;
}

.product-card__reviews-counter {
  font-size: 10px;
}

.product-card__price {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-text {
  color: #616070;
}

.product-card__price-value {
  font-size: 16px;
  font-weight: 700;
}

.product-card__price-value:before {
  content: "$";
}

.product-card__buy-btn {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.product-card__buy-btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

